'use client';

import Link from 'next/link';
import Image from 'next/image';
import { useEffect, useState } from 'react';
import { getArtists, getStrapiImageUrl, Artist } from '@/lib/strapi';

export default function ArtistsPage() {
  const [artists, setArtists] = useState<Artist[]>([]);
  
  useEffect(() => {
    getArtists().then(setArtists);
  }, []);

  // Sample artists data if Strapi is not connected
  const sampleArtists = [
    { name: 'Atish', slug: 'atish', image: '/images/camea.png' },
    { name: 'Ben Annand', slug: 'ben-annand' },
    { name: 'Dory', slug: 'dory' },
    { name: 'Halo Varga', slug: 'halo-varga' },
    { name: 'Jay Tripwire', slug: 'jay-tripwire' },
    { name: 'Justin Marchacos', slug: 'justin-marchacos' },
    { name: 'KMLN', slug: 'kmln' },
    { name: 'Lovestruckk', slug: 'lovestruckk' },
    { name: 'M.O.N.R.O.E', slug: 'monroe' },
    { name: 'Mark Slee', slug: 'mark-slee' },
    { name: 'Maxi Storrs', slug: 'maxi-storrs' },
    { name: 'MightyKat', slug: 'mightykat' },
    { name: 'Mr. C', slug: 'mr-c' },
    { name: 'Naveen G', slug: 'naveen-g' },
    { name: 'Nico Stojan', slug: 'nico-stojan' },
    { name: 'Nikita', slug: 'nikita' },
    { name: 'Holmar', slug: 'holmar' },
    { name: 'Galen', slug: 'galen' },
    { name: 'Nitin', slug: 'nitin' },
    { name: 'Bilaliwood', slug: 'bilaliwood' },
    { name: 'H-Foundation', slug: 'h-foundation' },
    { name: 'Ray Zuniga', slug: 'ray-zuniga' },
    { name: 'Philipp Jung', slug: 'philipp-jung' },
    { name: 'Anja Schneider', slug: 'anja-schneider' },
    { name: 'Atnarko', slug: 'atnarko' },
    { name: 'Sinca', slug: 'sinca', image: '/images/Sinca.jpeg' },
    { name: 'N-UM', slug: 'n-um' },
    { name: 'Matt Caines', slug: 'matt-caines' },
    { name: 'Reza Safinia', slug: 'reza-safinia' },
    { name: 'Saqib', slug: 'saqib' },
    { name: 'Beauty & The Beast', slug: 'beauty-the-beast' },
    { name: 'Formerly', slug: 'formerly' },
    { name: 'Camea', slug: 'camea', image: '/images/camea.png' },
    { name: 'Niki Sadeki', slug: 'niki-sadeki' }
  ];

  const displayArtists = artists.length > 0 ? artists.map(a => ({
    name: a.attributes.name,
    slug: a.attributes.slug,
    bio: a.attributes.bio,
    image: a.attributes.image?.data ? getStrapiImageUrl(a.attributes.image.data.attributes.url) : null
  })) : sampleArtists;

  return (
    <div className="container-custom">
      <section style={{ marginTop: '40px', marginBottom: '60px' }}>
        <h1 style={{ marginBottom: '40px' }}>ARTISTS</h1>
        
        <div style={{
          display: 'grid',
          gridTemplateColumns: 'repeat(auto-fill, minmax(250px, 1fr))',
          gap: '30px',
          marginLeft: '69px',
          marginRight: '69px'
        }}>
          {displayArtists.map((artist, index) => (
            <Link
              key={index}
              href={`/artists/${artist.slug}`}
              style={{
                display: 'block',
                textAlign: 'center',
                textDecoration: 'none',
                transition: 'transform 0.3s ease'
              }}
              onMouseEnter={(e) => e.currentTarget.style.transform = 'scale(1.05)'}
              onMouseLeave={(e) => e.currentTarget.style.transform = 'scale(1)'}
            >
              <div style={{
                width: '100%',
                paddingBottom: '100%',
                position: 'relative',
                backgroundColor: '#111',
                marginBottom: '15px',
                overflow: 'hidden'
              }}>
                {artist.image ? (
                  <Image
                    src={artist.image}
                    alt={artist.name}
                    fill
                    style={{ objectFit: 'cover' }}
                  />
                ) : (
                  <div style={{
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    transform: 'translate(-50%, -50%)',
                    fontSize: '3rem',
                    color: '#333',
                    fontFamily: "'dinBoldFont', sans-serif"
                  }}>
                    {artist.name.charAt(0)}
                  </div>
                )}
              </div>
              <h3 style={{
                fontSize: '1.8rem',
                fontFamily: "'dinFont', sans-serif",
                color: 'white',
                marginTop: '10px'
              }}>
                {artist.name}
              </h3>
            </Link>
          ))}
        </div>
      </section>


    </div>
  );
}