import axios from 'axios';

const strapiUrl = process.env.NEXT_PUBLIC_STRAPI_URL || 'http://localhost:1337';
const strapiToken = process.env.STRAPI_TOKEN;

// Create axios instance with default config
const api = axios.create({
  baseURL: `${strapiUrl}/api`,
  headers: {
    'Content-Type': 'application/json',
    ...(strapiToken && { Authorization: `Bearer ${strapiToken}` }),
  },
});

// Types for Strapi responses
export interface StrapiResponse<T> {
  data: T;
  meta?: {
    pagination?: {
      page: number;
      pageSize: number;
      pageCount: number;
      total: number;
    };
  };
}

export interface StrapiAttributes {
  createdAt: string;
  updatedAt: string;
  publishedAt: string;
}

export interface StrapiImage {
  data: {
    id: number;
    attributes: {
      url: string;
      alternativeText: string;
      caption: string;
      width: number;
      height: number;
      formats: {
        thumbnail?: ImageFormat;
        small?: ImageFormat;
        medium?: ImageFormat;
        large?: ImageFormat;
      };
    };
  };
}

interface ImageFormat {
  url: string;
  width: number;
  height: number;
}

// Content type interfaces
export interface Artist {
  id: number;
  attributes: StrapiAttributes & {
    name: string;
    slug: string;
    bio: string;
    image: StrapiImage;
    socialLinks?: {
      soundcloud?: string;
      instagram?: string;
      spotify?: string;
      facebook?: string;
    };
    featured: boolean;
  };
}

export interface Post {
  id: number;
  attributes: StrapiAttributes & {
    title: string;
    slug: string;
    content: string;
    excerpt: string;
    image: StrapiImage;
    category: {
      data: {
        id: number;
        attributes: {
          name: string;
          slug: string;
        };
      };
    };
    author?: {
      data: Artist;
    };
  };
}

export interface Page {
  id: number;
  attributes: StrapiAttributes & {
    title: string;
    slug: string;
    content: string;
    seo?: {
      metaTitle: string;
      metaDescription: string;
      shareImage?: StrapiImage;
    };
  };
}

export interface Event {
  id: number;
  attributes: StrapiAttributes & {
    title: string;
    slug: string;
    description: string;
    date: string;
    venue: string;
    city: string;
    image: StrapiImage;
    ticketLink?: string;
    artists: {
      data: Artist[];
    };
  };
}

// API functions
export async function getArtists(): Promise<Artist[]> {
  try {
    const response = await api.get<StrapiResponse<Artist[]>>('/artists', {
      params: {
        populate: '*',
        sort: 'name:asc',
      },
    });
    return response.data.data;
  } catch (error) {
    console.error('Error fetching artists:', error);
    return [];
  }
}

export async function getArtistBySlug(slug: string): Promise<Artist | null> {
  try {
    const response = await api.get<StrapiResponse<Artist[]>>('/artists', {
      params: {
        filters: { slug: { $eq: slug } },
        populate: '*',
      },
    });
    return response.data.data[0] || null;
  } catch (error) {
    console.error('Error fetching artist:', error);
    return null;
  }
}

export async function getPosts(limit?: number): Promise<Post[]> {
  try {
    const response = await api.get<StrapiResponse<Post[]>>('/posts', {
      params: {
        populate: '*',
        sort: 'publishedAt:desc',
        ...(limit && { pagination: { limit } }),
      },
    });
    return response.data.data;
  } catch (error) {
    console.error('Error fetching posts:', error);
    return [];
  }
}

export async function getPostBySlug(slug: string): Promise<Post | null> {
  try {
    const response = await api.get<StrapiResponse<Post[]>>('/posts', {
      params: {
        filters: { slug: { $eq: slug } },
        populate: '*',
      },
    });
    return response.data.data[0] || null;
  } catch (error) {
    console.error('Error fetching post:', error);
    return null;
  }
}

export async function getPageBySlug(slug: string): Promise<Page | null> {
  try {
    const response = await api.get<StrapiResponse<Page[]>>('/pages', {
      params: {
        filters: { slug: { $eq: slug } },
        populate: '*',
      },
    });
    return response.data.data[0] || null;
  } catch (error) {
    console.error('Error fetching page:', error);
    return null;
  }
}

export async function getEvents(upcoming: boolean = true): Promise<Event[]> {
  try {
    const now = new Date().toISOString();
    const response = await api.get<StrapiResponse<Event[]>>('/events', {
      params: {
        populate: '*',
        sort: 'date:asc',
        filters: upcoming ? { date: { $gte: now } } : { date: { $lt: now } },
      },
    });
    return response.data.data;
  } catch (error) {
    console.error('Error fetching events:', error);
    return [];
  }
}

// Helper function to get full image URL
export function getStrapiImageUrl(url: string): string {
  if (!url) return '';
  if (url.startsWith('http')) return url;
  return `${strapiUrl}${url}`;
}
